import {GetSchoolList} from '@/api/school'
import KsImage from '@/components/ks-components/image/Image.vue'
import KsFilter from '@/components/ks-components/filter/filter.vue'
import KsEmpty from '@/components/ks-components/empty/empty.vue'
import UniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue'
import UniIcons from '@/components/uni/uni-icons/uni-icons.vue'
export default {
	data(){
		return{
			loaded:false,
			tabIndex:0,//0小组，1问答，2作品，3资讯
			tabBars: ['小组','问答','作品','资讯'],
			getParameters:{
				pageindex:1,
				pagesize:10,
				key:'',
				orderid:0,
				type:0
			},
			sortList:[
				{type:0,name:'浏览量降序'},
				{type:1,name:'浏览量升序'},
				{type:2,name:'综合排序'}
			],//筛选列表
			sortLists:[
				{type:0,name:'全部'},
				{type:1,name:'已认证'},
				{type:2,name:'未认证'},
				{type:3,name:'最新一周'}
			],
			schoollist:[],//小组列表
			pageCount:0,
			moreStatus:'more',
			isshowprice:false
		}
	},
	components:{
		KsImage,
		KsEmpty,
		KsFilter,
		UniLoadMore,
		UniIcons
	},
	onLoad() {
		uni.showLoading({
		    title: '加载中'
		});
		uni.getSystemInfo({
			success:res=>{
				this.height = res.windowHeight
			}
		})
		this.loadPage()
	},
	onReachBottom(e){
		this.getParameters.pageindex++
		this.moreList()
	},
	onPullDownRefresh() {
		this.loadPage()
	},
	onShareAppMessage(){
		var that = this
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　　title: '科汛网校7.0的机构列表',
			path: '/pages/school/index',
	　　　　imageUrl: '',     //自定义图片路径，可以是本地文件路径、代码包文件路径或者网络图片路径，支持PNG及JPG，不传入 imageUrl 则使用默认截图。显示图片长宽比是 5:4
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　}
	　　};
	　　// 返回shareObj
	　　return shareObj;
	},
	methods: {
		getOrderid(id){
			this.getParameters.orderid = id
			this.loadPage()
		},
		getOrderids(id){
			this.getParameters.type = id
			this.loadPage()
		},
		openUrl(id){
			uni.navigateTo({
				url:'/pages/school/details?schoolid='+id
			})
		},
		openCourse(item){
			uni.navigateTo({
				url:'/pages/web/web?title='+item.name+'&photo='+item.photo+'&url='+encodeURIComponent(item.weburl)
			})
		},
		open(){
			uni.navigateTo({
			    url: '../course/search'
			});
		},
		search(){
			if(this.getParameters.key){
				uni.navigateTo({
					url:'/pages/course/search?key='+this.getParameters.key
				})
			}
		},
		//滚动加载更多的方法
		moreList(){
			if(this.getParameters.pageindex > this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'loading'
				this.loadPage('more')
			}
			
		},
		//初始数据的请求
		loadPage(status){
			this.isshowprice = this.$store.state.isshowprice
			if(status=='more'){
				GetSchoolList(this.getParameters).then(res=>{
					this.moreStatus = 'more'
					if(res.result){
						this.schoollist = this.schoollist.concat(res.data)
						
					}
					this.loaded = true
				})
			}else{
				this.getParameters.pageindex = 1
				GetSchoolList(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.pageCount
						this.schoollist = res.data
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
					}else{
						this.schoollist = []
						this.pageCount = 0
					}
					this.loaded = true
					uni.stopPullDownRefresh();
					uni.hideLoading();
				})
			}
		}
		
	}
}
